html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  display: inline-block;
  background-color: #E08709;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  text-align: center;
  border: none;
  border-radius: 0.3rem;
}
.boton-amarillo:hover {
  background-color: #b96f07;
  cursor: pointer;
}

.boton-amarillo-block {
  display: block;
  background-color: #E08709;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  text-align: center;
  border: none;
  border-radius: 0.3rem;
}
.boton-amarillo-block:hover {
  background-color: #b96f07;
  cursor: pointer;
}

.boton-verde {
  display: inline-block;
  background-color: #1b67ad;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  text-align: center;
  border: none;
  border-radius: 0.3rem;
}
.boton-verde:hover {
  background-color: #15528a;
  cursor: pointer;
}

.boton-rojo-block {
  display: block;
  background-color: rgb(189, 18, 18);
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  text-align: center;
  border: none;
  border-radius: 0.3rem;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #980e0e;
  cursor: pointer;
}

.boton-verde-block {
  display: block;
  background-color: #1b67ad;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  text-align: center;
  border: none;
  border-radius: 0.3rem;
}
.boton-verde-block:hover {
  background-color: #15528a;
  cursor: pointer;
}

.boton {
  display: block;
  margin: 0 auto;
  margin-top: 2rem;
}

.contendor-formulario {
  width: 50%;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
  font-family: "Quicksand", sans-serif;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  border: 1px solid #f0efef;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 20rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.logo {
  margin: 0 auto;
  width: 5rem;
  margin-top: 2rem;
}

.titulo {
  font-family: "Quicksand", sans-serif;
  text-align: center;
  font-weight: 300;
  margin-top: 0;
  margin-bottom: 2rem;
}

.error {
  color: rgb(189, 18, 18);
  text-align: center;
  margin: 2rem;
}

@media (min-width: 768px) {
  .logo {
    margin: 0 auto;
    width: 5rem;
    margin-top: 2rem;
  }
  .titulo {
    font-family: "Quicksand", sans-serif;
    text-align: center;
    font-size: 3rem;
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 2rem;
  }
  .error {
    color: rgb(189, 18, 18);
    text-align: center;
    margin: 2rem;
    font-size: 2rem;
  }
}
@media (min-width: 170px) {
  .error {
    color: rgb(189, 18, 18);
    text-align: center;
    font-size: 1rem;
    margin: 2rem;
  }
  .titulo {
    font-size: 3rem;
  }
  .logo {
    margin: 0 auto;
    width: 3rem;
    margin-top: 2rem;
  }
  .logo legend {
    width: 1rem;
  }
}
body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-thumb {
  background: #7065F9;
  background: linear-gradient(90deg, rgb(112, 101, 249) 0%, rgb(3, 201, 215) 100%);
  border-radius: 10px;
}

html {
  background-color: #fff;
  box-sizing: border-box;
}

body {
  font-family: "DM Sans", sans-serif;
  background-color: #F9F9FC;
}

.btn-custom {
  background: #7065F9;
  background: linear-gradient(90deg, rgb(112, 101, 249) 0%, rgb(3, 201, 215) 100%);
  color: #fff;
  border-color: #7065F9;
}

.btn-custom:hover {
  color: #fff;
  background: #7065F9;
  background: linear-gradient(90deg, rgb(3, 201, 215) 24%, rgb(112, 101, 249) 96%);
}

.user-img {
  width: 2rem;
  height: 2rem;
}

#wrapper {
  overflow-x: hidden;
}

.sidebar {
  background-color: #F5F5F9;
  transition: margin 0.25s ease-out;
  -webkit-transition: margin 0.25s ease-out;
  -moz-transition: margin 0.25s ease-out;
  -ms-transition: margin 0.25s ease-out;
  -o-transition: margin 0.25s ease-out;
}

.sidebar__nav {
  padding-left: 0;
}

.sidebar__list {
  width: 15rem;
  list-style-type: none;
  padding: 2rem 0 2rem 2.5rem;
  position: relative;
}

.sidebar__list:hover i {
  color: #7065F9;
}

.sidebar__list:hover a {
  color: #7065F9;
}

.sidebar__list::before {
  position: absolute;
  content: "";
  right: 0px;
  top: 0;
  height: 100%;
  background-color: #7065F9;
  transition: all ease-in 0.2s;
  -webkit-transition: all ease-in 0.2s;
  -moz-transition: all ease-in 0.2s;
  -ms-transition: all ease-in 0.2s;
  -o-transition: all ease-in 0.2s;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
}

.sidebar__list:hover::before {
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  background-color: #7065F9;
  width: 5px;
  height: 100%;
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
}

.active {
  background-color: #EFEEF9;
  border-right: 5px solid #7065F9;
}

.active:hover {
  border-right: 0px;
}

.active i {
  color: #7065F9;
}

.active a {
  color: #7065F9;
}

.sidebar__link {
  font-size: 1.2rem;
  margin-left: 1rem;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.7);
}

.sidebar__list i {
  font-size: 1.3rem;
}

#page-content-wrapper {
  min-width: 0;
  width: 100%;
}

.top-search {
  width: 40rem;
}

#wrapper.toggled .sidebar {
  margin-left: -15rem;
}

#menu-toggle {
  cursor: pointer;
  font-size: 1.7rem;
}

.main-part {
  padding: 40px !important;
}

/* Table styling  */
.product-img {
  height: 3rem;
  width: 3rem;
  object-fit: cover;
}

.dataTable thead tr th {
  padding: 8px;
  border-bottom: 1px solid #666;
}

.dataTable tbody tr td {
  padding: 8px;
  border-bottom: 1px solid #EFEEF9;
}

/* Styling for small screens  */
@media (max-width: 768px) {
  .sidebar {
    margin-left: -15rem;
  }
  #page-content-wrapper {
    min-width: 100vw;
  }
  #wrapper.toggled .sidebar {
    margin-left: 0;
  }
  .main-part {
    padding: 10px !important;
  }
  .gear-btn {
    margin-top: 20px;
  }
}
.card {
  display: flex;
}

@media (min-width: 480px) {
  .card {
    flex-direction: row;
  }
}